@import url('https://fonts.googleapis.com/css2?family=Google+Sans:wght@400;500;700&display=swap');

/* Base font settings */
html {
  font-size: 100%;
}

body {
  margin: 0;
  font-family:'Google Sans','Roboto',-apple-system, 'Segoe UI','Quicksand', BlinkMacSystemFont, 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: clamp(0.75rem, 0.7vw, 0.8125rem);
  line-height: 1.5;
}

/* Heading styles */
h1 { font-size: clamp(1.5rem, 4vw, 2rem); }
h2 { font-size: clamp(1.25rem, 3.5vw, 1.75rem); }
h3 { font-size: clamp(1.125rem, 3vw, 1.5rem); }
h4 { font-size: clamp(1rem, 2.5vw, 1.25rem); }
h5 { font-size: clamp(0.875rem, 2vw, 1.125rem); }
h6 { font-size: clamp(0.75rem, 1.8vw, 1rem); }

/* Text elements */
p, span {
  font-size: clamp(0.75rem, 1.5vw, 0.875rem);
}

/* Interactive elements */
button, label {
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}