}

/* Horizontal scrolling container */
.tableScrollContainer {
  width: 100%;
  flex: 1;
  display: block;
  min-width: max-content;
}

/* Table structure */
.tableContent {
  min-width: max-content;
  display: flex;
  flex-direction: column;
  width: 100%;
}

/* Table header styles */
.tableHeader {
  position: sticky;
  top: 0;
  z-index: 10;
  background: #f8f9fa;
  border-bottom: 2px solid #e2e8f0;
}

.headerRow {
  display: flex;
  width: max-content;
  min-width: 100%;
}

.headerCell {
  padding: 12px 16px;
  font-weight: 600;
  color: #4a5568;
  background-color: #f8f9fa;
  border-bottom: 2px solid #e2e8f0;
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

/* Table body styles */
.tableBody {
  overflow-y: auto;
  flex: 1;
}

.tableRow {
  display: flex;
  width: max-content;
  min-width: 100%;
  border-bottom: 1px solid #edf2f7;
}

.tableRow:hover {
  background-color: #fff5f5; /* Keep the red theme for fee warnings */
}

.tableCell {
  padding: 12px 16px;
  flex-shrink: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  align-items: center;
}

/* Loading overlay styles */
.loadingOverlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loadingOverlay span {
  margin-top: 10px;
  color: #4a90e2;
  font-size: 16px;
  font-weight: 500;
}

/* Custom scrollbar styles */
.tableScrollContainer::-webkit-scrollbar,
.tableBody::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.tableScrollContainer::-webkit-scrollbar-track,
.tableBody::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.tableScrollContainer::-webkit-scrollbar-thumb,
.tableBody::-webkit-scrollbar-thumb {
  background: #cbd5e0;
  border-radius: 4px;
}

.tableScrollContainer::-webkit-scrollbar-thumb:hover,
.tableBody::-webkit-scrollbar-thumb:hover {
  background: #a0aec0;
}

