
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Remove duplicate font declarations */
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

/* Remove duplicate html and body font declarations */
html, body {
  height: 100%;
  margin: 0;
  overflow: hidden;
}

/* Update login form elements to use clamp values */
.login-container input {
  margin: 0.625rem;
  padding: 0.625rem;
  width: 12.5rem;
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
}

.login-container button {
  padding: 0.625rem 1.25rem;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Tùy chỉnh thanh cuộn cho toàn bộ trang */
html, body {
  height: 100%; /* Đảm bảo chiều cao đầy đủ */
  margin: 0;
  overflow: hidden; /* Ẩn thanh cuộn mặc định trên body */
}

.app-container {
  display: flex;
  height: 100%;
  overflow: hidden; /* Đảm bảo không có thanh cuộn mặc định */
}

.sidebar {
  flex: 10;
  background-color: #2c3e50;
  color: white;
  min-height: 100vh; /* Đảm bảo chiều cao tối thiểu */
  overflow-y: auto; /* Thêm thanh cuộn riêng cho sidebar */
  scrollbar-width: thin; /* Thu hẹp thanh cuộn cho Firefox */
  scrollbar-color: #ccc #2c3e50; /* Màu thanh cuộn cho Firefox */
}

.sidebar::-webkit-scrollbar {
  width: 8px; /* Độ rộng thanh cuộn dọc */
}

.sidebar::-webkit-scrollbar-track {
  background: #2c3e50; /* Màu nền thanh cuộn */
}

.sidebar::-webkit-scrollbar-thumb {
  background: #444; /* Màu thanh kéo */
  border-radius: 4px; /* Bo tròn thanh kéo */
}

.sidebar::-webkit-scrollbar-thumb:hover {
  background: #555; /* Màu khi hover */
}

.main-content {
  flex: 90;
  background-color: #f2f4f7;
  padding: 20px;
  overflow-y: auto; /* Cho phép cuộn nội dung chính */
  scrollbar-width: thin; /* Thanh cuộn nhỏ trên Firefox */
  scrollbar-color: #ccc #ffffff; /* Màu thanh cuộn */
}

.main-content::-webkit-scrollbar {
  width: 6px;
}

.main-content::-webkit-scrollbar-track {
  background: #fafafa; /* Màu nền thanh cuộn */
  border-radius: 16px;
}

.main-content::-webkit-scrollbar-thumb {
  background: #888; /* Màu thanh kéo */
  border-radius: 16px;
}

.main-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login-container input {
  margin: 10px;
  padding: 10px;
  width: 200px;
}

.login-container button {
  padding: 10px 20px;
  background-color: #2c3e50;
  color: white;
  border: none;
  cursor: pointer;
}

.login-container button:hover {
  background-color: #34495e;
}