/* Sidebar header */
.sidebar-header {
  text-align: center;
  padding: 10px;
  position: sticky;
  top: 0;
  background-color: #1a1b21;
  z-index: 10;
}

.sidebar-header .logo {
  max-width: 51px; /* Reduced from 60px */
  height: auto;
  border-radius: 50%;
  margin-bottom: 7px;
  margin-top: 4px;
}

/* Sidebar header */
.sidebar-header .university-name {
  font-size: clamp(0.75rem, 1.5vw, 0.875rem);
  letter-spacing: 0.04rem;
  font-weight: 500;
}

.menu-section > .ps-menu-button {
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem) !important;
  padding-left: 1rem !important;
  margin: 0.25rem 0 !important;
}

.menu-icon {
  margin-right: 0.75rem !important;
  min-width: 1.5rem !important;
  font-size: clamp(0.875rem, 1.8vw, 1rem);
}

.sidebar-footer button {
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
  width: 2.5rem;
  height: 2.5rem;
}

.modal p {
  color: #2d2c2c;
  font-size: clamp(0.75rem, 1.5vw, 0.875rem);
}

.confirm-button, .cancel-button {
  font-size: clamp(0.75rem, 1.2vw, 0.8125rem);
  padding: 0.5rem 1rem;
}

/* Sidebar Menu */
.app-sidebar .ps-menu-button:hover {
  background-color: #2c5282 !important;
}

.app-sidebar .ps-active {
  background-color: #2c5282;
}

/* Menu icon animation */
.menu-icon {
  transition: transform 0.3s ease;
  margin-right: 12px !important;
  min-width: 24px !important;
  font-size: 20px;
}

/* SubMenu title styling */
.ps-submenu-content {
  background-color: #1a1b21 !important;
}

.ps-menu-label {
  color: inherit;
}

/* Update submenu alignment */
.menu-section > .ps-menu-button {
  justify-content: flex-start !important;
  padding-left: 16px !important;
  font-size: 0.7rem !important;
  color: #767373 !important;
  text-transform: none;
  border-bottom: 1px solid #444 !important;
  border-radius: 0 !important;
  margin: 5px 0 !important;
  display: flex !important;
  align-items: left !important;
}

/* Submenu items indent */
.ps-submenu-content .ps-menu-button {
  padding-left: 32px !important;
}

.ps-submenu-content .ps-menu-button:hover {
  padding-left: 44px !important;
}

/* Make sidebar scrollable */
.ps-sidebar-container {
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #444 #1E1E2D;
}

.ps-sidebar-container::-webkit-scrollbar {
  width: 6px;
}

.ps-sidebar-container::-webkit-scrollbar-track {
  background: #1E1E2D;
}

.ps-sidebar-container::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 5px;
}

.ps-sidebar-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Sidebar Footer */
.sidebar-footer {
  width: 100%;
  padding: 8px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 5pt;
  background-color: #232429;
  position: sticky;
  bottom: 0;
  z-index: 10;
  border-top: 1px solid #2d2e35;
  box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1);
}

.sidebar-footer button {
  background: #1a1b21;
  border: 2px solid #2d2e35;
  color: #a1a3a1;
  font-size: 1.2em;
  cursor: pointer;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

/* Logout button */
.sidebar-footer .logout-button:hover {
  background: red;
  color: white;
  transform: translateY(-15px);
}

/* Profile button */
.sidebar-footer .profile-button:hover {
  background: yellow;
  color: white;
  transform: translateY(-15px);
}

/* Toggle sidebar button */
.sidebar-footer .toggle-sidebar-button:hover {
  background: blue;
  color: white;
  transform: translateY(-15px);
}

/* Modal overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: #fff;
  padding: 16px;
  border-radius: 6px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.modal p {
  color: #2d2c2c;
  font-size: 0.875rem;
}

.modal-buttons {
  margin-top: 12px;
}

.confirm-button {
  background-color: red;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  font-size: 0.875rem;
}

.cancel-button {
  background-color: grey;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
}

.confirm-button:hover {
  background-color: darkred;
}

.cancel-button:hover {
  background-color: darkgrey;
}

/* Collapsed sidebar styles */
.ps-collapsed .sidebar-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ps-collapsed .sidebar-header .logo {
  margin-bottom: 0;
}

/* Fix for menu section styling when collapsed */
.ps-collapsed .menu-section > .ps-menu-button {
  justify-content: center !important;
  padding: 8px !important;
}

/* Collapsed sidebar styles */
.ps-collapsed .sidebar-footer {
  justify-content: center;
}

.ps-collapsed .sidebar-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ps-collapsed .sidebar-header .logo {
  margin-bottom: 0;
}

/* Ẩn tiêu đề SubMenu khi collapsed */
.ps-collapsed .menu-section > .ps-menu-button .ps-menu-label {
  display: none;
}

.ps-collapsed .menu-section > .ps-menu-button {
  justify-content: center;
  padding: 8px !important;
  border-bottom: none !important;
}

.ps-collapsed .ps-submenu-content {
  background-color: #1a1b21 !important;
}

.ps-menu-button {
  display: flex !important;
  align-items: center !important;
  justify-content: flex-start !important;
  padding-left: 16px !important;
}