.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.pageInfo {
  font-size: 14px;
  color: #666;
}

.pageControls {
  display: flex;
  align-items: center;
  gap: 5px;
}

.pageButton {
  min-width: 32px;
  height: 32px;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  transition: all 0.3s;
}

.pageButton:hover:not(:disabled) {
  border-color: #1890ff;
  color: #1890ff;
}

.pageButton:disabled {
  cursor: not-allowed;
  color: #d9d9d9;
}

.pageButton.active {
  border-color: #1890ff;
  background-color: #1890ff;
  color: #fff;
}

.ellipsis {
  margin: 0 5px;
}

@media (min-width: 768px) {
  .pagination {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
}


.pageInput {
  width: 40px;
  padding: 4px;
  text-align: center;
  border: 1px solid #e2e8f0;
  border-radius: 4px;
  margin: 0 4px;
  font-size: 14px;
}

.pageInput:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

.pageInput::placeholder {
  color: #a0aec0;
}