.container {
  position: relative;
  width: 100%;
}

.selectInput {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  outline: none;
}

.selectInput:focus {
  border-color: #4a90e2;
}

.dropdown {
  position: fixed; /* Changed from absolute to fixed */
  background: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-top: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  z-index: 9999;
  min-width: 300px; /* Added minimum width */
  max-width: 500px; /* Added maximum width */
}

.item {
  padding: 8px 12px;
  cursor: pointer;
  background: white;
  white-space: normal; /* Allow text wrapping */
  line-height: 1.4;
}

.item:hover {
  background-color: #f5f5f5;
}

.selected {
  background-color: #e6f3ff;
}

.loadingItem {
  padding: 8px 12px;
  color: #999;
  font-style: italic;
  background: white;
}