.container {
  padding: 20px;
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.filterContainer {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 20px;
}

.typeSelector {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 16px;
  background-color: white;
  border-radius: 8px;
}

.typeSelector label {
  font-weight: 500;
  color: #333;
  min-width: 100px;
}

.chartsContainer {
  border-radius: 0px 16px;
  background-color: white;
  display: flex;
  flex-direction: column;
  gap: 24px;
  overflow-y: auto;
  flex: 1;
  min-height: 0; /* Important for Firefox */
  margin-bottom: 10px;
}

.chartCard {
  background: white;
  border-radius: 16px;
  padding: 24px;
  flex-shrink: 0; /* Prevents chart cards from shrinking */
}

.chartHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.chartWrapper {
  height: 400px;
  position: relative;
}

.loading {
  text-align: center;
  padding: 40px;
  font-size: 18px;
  color: #666;
}

h2 {
  margin: 0 0 20px 0;
  color: #333;
  font-size: 1.5rem;
}

.select {
  padding: 8px 12px;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  font-size: 14px;
  width: 200px;
  background-color: white;
  cursor: pointer;
}

.select:hover {
  border-color: #40a9ff;
}

.select:focus {
  outline: none;
  border-color: #40a9ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}