.notificationItem {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    text-align: left; /* Căn lề trái */
}

.notificationItem:last-child {
    border-bottom: none; /* Không có đường viền cho item cuối cùng */
}

.notificationItem h3 {
    margin: 0;
    font-size: 18px;
    text-align: left; /* Căn lề trái cho tiêu đề */
}

.notificationItem p {
    margin: 5px 0;
    color: #555;
}

.notificationItem .notificationTime {
    font-size: 12px;
    color: #999;
}

.notificationNote {
    font-weight: 500;
    font-size: 14px; /* Kích thước chữ cho ghi chú */
    color: #333; /* Màu sắc cho ghi chú */
    margin-bottom: 5px; /* Khoảng cách dưới ghi chú */
}

.statusContainer{
    display: inline-flex;
    gap: 8px;
    align-items: center;
    justify-content: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.actions {
  display: flex;
  gap: 8px;
}