.createTab {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
  background: #fff;
  /* height: calc(100vh - 100px); */
  /* overflow-y: auto; */
}

.createTab::-webkit-scrollbar {
  width: 6px;
}

.createTab::-webkit-scrollbar-thumb {
  background-color: #e2e8f0;
  border-radius: 3px;
}

.createTab::-webkit-scrollbar-track {
  background: transparent;
}

.formSection {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.label {
  font-weight: 500;
  color: #4a5568;
  font-size: 0.95rem;
}

.input,
.textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  font-size: 0.95rem;
  transition: all 0.2s;
}

.input:focus,
.textarea:focus {
  outline: none;
  border-color: #3182ce;
  box-shadow: 0 0 0 3px rgba(49, 130, 206, 0.1);
}

.textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  resize: vertical;
}

.textarea:focus {
  outline: none;
  border-color: #4299e1;
  box-shadow: 0 0 0 1px rgba(66, 153, 225, 0.5);
}

.radioGroup {
  display: flex;
  gap: 16px;
  margin: 8px 0;
}

.radioLabel {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  color: #4a5568;
  font-size: 0.95rem;
}

.radioLabel input[type="radio"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: #3182ce;
}

.actions {
  margin-top: 32px;
  margin-bottom: 20px;
  display: flex;
  justify-content: flex-end;
}